import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Configuracao_helpdesk } from '../../../modelo/Configuracao_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'configuracao_helpdesk-dados-edit',   templateUrl: 'configuracao_helpdesk_dadosEdit.component.html' }) export class Configuracao_helpdeskDadosEditComponent implements OnInit {    configuracao_helpdesk: Configuracao_helpdesk;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.configuracao_helpdesk.Configuracao_helpdesk";                 let id = +params['id'];         this.configuracao_helpdesk = new Configuracao_helpdesk();         this.configuracao_helpdesk.cd_configuracao_helpdesk = id;         o.objetoJson = this.configuracao_helpdesk;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a configuracao_helpdesk. ';       }      this.configuracao_helpdesk = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


